    <el-aside width="210px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-active="open_index" @select="openIndexHandle" unique-opened=true>
            <el-submenu index="66">
                    <template slot="title"><i class="iconfont icon-fl-shuju"></i> 数据统计</template>
                    <el-menu-item-group>
                        <el-menu-item index="666" @click="goto('dataAnalysis')">日常统计</el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index="11">
                <template slot="title"><i class="iconfont icon-kehuqunzu"></i> 权限管理</template>
                <el-menu-item-group>
                    <el-menu-item index="1" @click="goto('powerRole')">角色管理</el-menu-item>
                    <el-menu-item index="2" @click="goto('powerUser')">用户管理</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="22">
                <template slot="title"><i class="iconfont icon-zaixianchongzhi"></i> APP用户管理</template>
                <el-menu-item-group>
                    <el-menu-item index="3" @click="goto('userList')">用户列表</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="33">
                <template slot="title"><i class="iconfont icon-consumption"></i> 店铺管理</template>
                <el-menu-item-group>
                    <el-menu-item index="5" @click="goto('categoryList')">分类管理</el-menu-item>
                    <el-menu-item index="4" @click="goto('storeList')">店铺列表</el-menu-item>                    
                    <el-menu-item index="13" @click="goto('storeLimit')">店铺限制</el-menu-item>
                    <el-menu-item index="14" @click="goto('moneyRecord')">平台打款记录</el-menu-item>

                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="44">
                <template slot="title"><i class="iconfont icon-goods-copy"></i> 商品管理</template>
                <el-menu-item-group>
                    <el-menu-item index="8888" @click="goto('goodsList')">商品列表</el-menu-item>
                    <el-menu-item index="7" @click="goto('goodsSpecial')">商品专题</el-menu-item>
                    <el-menu-item index="8" @click="goto('goodsComment')">评价管理</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="55">
                <template slot="title"><i class="iconfont icon-fl-dingdan"></i>订单管理</template>
                <el-menu-item-group>
                    <el-menu-item index="9" @click="goto('orderList')">订单列表</el-menu-item>
                    <el-menu-item index="95464" @click="goto('orderCompute')">订单结算</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
