                        <el-menu-item index="10" @click="goto('whatfuckeryouwannago')">预留</el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index="245">
                <template slot="title"><i class="iconfont icon-fl-dingdan"></i>APP更新管理</template>
                <el-menu-item-group>
                    <el-menu-item index="12" @click="goto('AndroidUpdate')">APP版本列表</el-menu-item>
                </el-menu-item-group>
            </el-submenu>            
        </el-menu>
    </el-aside>
</template>
<script>
    import { mapState, mapMutations, mapActions } from 'vuex';
    import store from '../../vuex/store'
    export default {
        name: 'Left',
        data() {
            return {
                open_index: sessionStorage.getItem('openIndex') || '',// 当前打开菜单的index
            }
        },
        methods: {
            goto(path) {
                this.$router.push({ name: path })
            },
            openIndexHandle(data, indexPath) {
                sessionStorage.setItem('openIndex', data)
            }
        }
    }

</script>
<style>
    .el-menu-item-group__title {
        display: none
    }
</style>


