/*
|--------------------------------------------------------------------------
| 组件 商品管理\商品列表  path：/home/bookList
|--------------------------------------------------------------------------
|
| author：陈胜云
| data： vuex.userInfo
| DOTO:  备注
|
*/
<template>
    <div class="bookList">

        <div class="head-box">
            <span class="text">商品列表</span>
        </div>
        <el-form :inline="true"  class="search-wrap">
            <el-form-item label="模糊搜索" size="mini">
                            <el-input placeholder="请输入内容" filterable  v-model='queryCondition.key_word' class="input-with-select">
                              <el-select v-model="queryCondition.type" slot="append" placeholder="请选择" >
                                <el-option label="手机号" value="2"></el-option>
                                <el-option label="订单号" value="1"></el-option>
                                <el-option label="台号" value="3"></el-option>
                              </el-select>
                            </el-input>
            </el-form-item>
            <el-form-item label="所在区域" size="mini">
                <el-select v-model="queryCondition.region_name">
                        <el-option label="全部" value="true"></el-option>
                        <el-option label="包间" value="false"></el-option>
                        <el-option label="VIP" value="true"></el-option>
                        <el-option label="卡座" value="false"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="订单状态" size="mini">
                <el-select v-model="queryCondition.status">
                        <el-option label="全部" value=""></el-option>
                        <el-option label="未结账" value="0"></el-option>
                        <el-option label="已结账" value="1"></el-option>
                        <el-option label="已撤单" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="支付方式" size="mini">
                <el-select v-model="queryCondition.s_EQ_status">
                    <el-option
                        v-for="item in payTypeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item size="mini">
                <el-button type="primary" @click="search()">筛选</el-button>
                <el-button type="primary" @click="clear()">清空</el-button>
            </el-form-item>
        </el-form>

        <el-table :data="tableData" :border="true" stripe style="width: 100%" >
            <el-table-column prop="id" label="订单号" align="center"></el-table-column>
            <el-table-column prop="table_number" label="桌台名称" align="center"></el-table-column>
            <el-table-column prop="order_date" label="下单时间" align="center"></el-table-column>
            <el-table-column prop="end_date" label="结束时间" align="center">          
            </el-table-column>
            <el-table-column prop="sale_price" label="订单金额（￥）" align="center"></el-table-column>
            <el-table-column prop="finally_price" label="应收金额（￥）" align="center"></el-table-column>
            <el-table-column prop="finsh_tag" label="订单状态" align="center">
                 <template slot-scope="scope">
                    {{scope.row.status == 1 ? "上架":"下架"}}
                 </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="goDetails(scope.row.id)">查看</el-button>
                    </template>
            </el-table-column>
        </el-table>

        <div class="page">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="param.page"
                    :page-sizes="[10, 20, 50]"
                    :page-size="param.per_page"
                     layout="total, sizes, prev, pager, next, jumper"
                    :total="param.total">
                </el-pagination>
        </div>

    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 你的引入描述
    import {mapState,mapMutations,mapActions} from 'vuex';
    import api from '../../../../service/orderManage/index.js'
  
    export default {
      name: 'powerRole',
      data() {
        return {
            queryCondition:{
              type:'',//模糊查询的类别
              key_word:"",//模糊查询内容
              region_name:"",//所在区域
              status:"",//订单状态
              pay:"",//支付方式
              token:"",
              page:1,
              per_page:10
            },
            payTypeOptions:[],
            tableData:[
                {
                    order_number:1994
                }
            ],
            param:{
                page:1,
                per_page:10,
                total:0
            }
        }
      },
      computed: {
            /*catch store.data*/
            ...mapState({
                token_: state => state.token_,
            }),
    },
     mounted () {
         this.payType();
        this.initTable();
      },
      methods: {
        
        /**
         * 初始化表格数据
         * @param 参数1说明 {arr} XXX
         * @param 参数2说明 {obj} XXX
         * @returns 返回值的类型 {boolean}
         */
        async initTable(){
            this.queryCondition.token = this.token_;
            const {data,code} = await api.getOrderList(this.queryCondition);
            this.tableData = data.data;
        },

        async payType(){
            const {data,code} = await api.getPayType();
            this.payTypeOptions = data.map((item)=>{
                let obj = {
                    value:item.id,
                    label:item.pay_name
                }
                return obj;
            });
        },


        // 搜索按钮
        search(){
            this.initTable();
        },

        // 清空按钮
        clear(){
            this.queryCondition = {
              type:'',//模糊查询的类别
              key_word:"",//模糊查询内容
              region_name:"",//所在区域
              status:"",//订单状态
              pay:"",//支付方式
              page:1,
              per_page:10
            };
            this.initTable();
        },

        /**
         * 分页相关
         */
        handleSizeChange(num){
            this.queryCondition.page = num;
            this.initTable();
        },
        handleCurrentChange(num){
            this.queryCondition.per_page = num;
            this.initTable();
        },
        goDetails(id){
              this.$router.push({
                name: "orderDetails",
                query: { id: id }
              });
        },

      }
    }
  </script>
<style lang = "less" scoped>
    /* @import url("//unpkg.com/element-ui@2.4.11/lib/theme-chalk/index.css"); */
    @import '../../../../globalCss/var.less';
    @import "style.less";
</style>


