<template>
  <div class="home">
    <myHeader></myHeader>
    <div class="home-center">
      <div class="home-center-row">
        <div>
          <!--背景图-->
        </div>
        <div class="home-center-row-login-todo">
          <p class="title">用户登录</p>
          <div class="content">
            <p><input type="text" v-model="mobile" placeholder="请输入手机号" @keyup="toLogin($event)" />
              <!--<label><img src="../../assets/img/login/iPhone.png" alt=""></label>-->
            </p>
            <p><input type="password" v-model="password" placeholder="请输入密码" @keyup="toLogin($event)" />
              <p><label class="remberPwd"><input type="checkbox" v-model="isRemberPwd">记住密码</label><span></span></p>
              <!--<label><img src="../../assets/img/login/password.png" alt=""></label></p>-->
              <p><input type="button" @click="login" value="登 录" /></p>
              <!--<p><input type="checkbox"><label>记住密码？</label></p>-->
          </div>
        </div>
      </div>
      <p class="home-center-bottom-title"> 成都市云中奇境科技有限公司版权所有</p>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations,
    mapActions
  } from 'vuex';
  import myHeader from '../common/header'
  import 'regenerator-runtime/runtime'
  import {
    loginHandle,
    signLoginHandle,
    getUserPermission
  } from '../../service/api'
  import cookie from '../../utils/cookie.js'

  export default {
    name: 'login',
    components: {
      myHeader
    },
    data() {
      return {
        mobile: "superadmin", // 手机号
        password: "123456", // 密码
        isRemberPwd: false, // 是否记住密码
        popup: false,
        treeData: [],
        defaultProps: {
          children: '_child',
          label: 'BMMC',
          value: "BMBM"
        },
        checkedDepartment: ""
      }
    },
    mounted() {

    },
    methods: {
      /*catch store.mutation*/
      ...mapMutations(['setName', 'setAuthority', 'saveDepartment']),
      /*catch store.action*/
      ...mapActions(['SAVE_USERINFO', 'SET_AUTHORITY', 'SAVE_OPTIONS']),
      async login() {

        // 开发UI周期的临时无条件登录
        // this.$router.push({
        //     path: "/home"
        // });
        // return false;


        let userInfo = [{},
          [],
          []
        ];
        this.SAVE_USERINFO(userInfo);
        if (!this.mobile || !this.password) {
          this.$utils.openMessgeTip("用户名或密码不能为空", '提示')
          return;
        } 
        let loginParam = {
          "userName":"superadmin",
          "userPass":"123456"
        };
        const {
          code,
          data,
          msg
        } = await loginHandle(loginParam);
        if (code == 1000) {
          this.SAVE_USERINFO(data);
          this.popup = true;
          this.treeData = data[2];
          if (this.isRemberPwd) {
            let nameAndPwd = {
              mobile: this.mobile,
              password: this.password
            }
            cookie.setCookie("nameAndPwd", JSON.stringify(nameAndPwd), 7)
          } else {
            cookie.setCookie("nameAndPwd", null, 7)
          }
          let type_=0;
          if(data.enableTwoFactorAuth == true && data.twoFactorAuth == true)
          type_=1;
          if(data.enableTwoFactorAuth == true && data.twoFactorAuth == false)
           type_=2;
        // this.getUserPermission_(type);
          this.getUserPermission_('go'); // 临时的开发版登录
        } else {
          this.$utils.openMessgeTip(msg, '提示')
        }
      },
      async signLogin() {
        const {
          code,
          data,
          message
        } = await signLoginHandle(this.$route.query.timestamp, this.$route.query.mobile, this.$route.query.signature);
        if (code == 200) {
          this.SAVE_USERINFO(data);
          this.$router.push({
            path: '/home'
          });
        } else {
          this.$utils.openMessgeTip(message, '提示')
        }
      },
      handleNodeClick(data) {
        this.checkedDepartment = data;
      },
      checkDepartment() { //完成部门选择 点击OK 触发的事件
        //        if (this.checkedDepartment) {
        //          this.saveDepartment(this.checkedDepartment);
        //          this.$router.push({ path: "/home" })
        //        } else {
        //          this.$utils.openMessgeTip('请先选择一个部门!','提示')
        //        }
      },
      // toLogin(){
      //      alert(12121)
      // }
      toLogin(ev) {
        if (ev.keyCode == 13) {
          this.login()
        }
      },

      async getUserPermission_(type){
        const {code,data,msg} = await getUserPermission();
        console.log(type,"~")
        if(true){
          if(type ==1){
             this.$router.push({
              path: '/yesUcan'
            });
          }if(type ==2){
             this.$router.push({
              path: '/canI'
            });
          }else{
             this.$router.push({
              path: "/home/dataAnalysis"
          })
          }
        }else{
          alert(msg);
        }
      },

    }
  }
</script>

<style lang="less">
  @import "./style.less";
</style>


