import {get,post} from './../axiosConfig'
import reHttp from './../http'


/******************** 商品管理 —— 商品列表  ********************/

/**
 * 查询用户列表
 */
export function getOrderList (param) {
    return post(`${reHttp}/cashier/invoicing/orderList`,param)
}

/**
 * 支付方式查询
 */
export function getPayType (param) {
    return post(`${reHttp}/system/single/paymentList`,param)
}

/**
 * 订单详情
 */
export function getOrderDetail (param) {
    return post(`${reHttp}/cashier/invoicing/orderDetail`,param)
}



export default {
    getOrderList,
    getPayType,
    getOrderDetail
}


