<template>
  <div class="home">
    <myHeader></myHeader>
    <div class="container">
      <div class="crmContent">
          <Left class="crmLeft"></Left>
          <div class="crmRight">
              <router-view/>
          </div>
      </div>
      <div class="common-footer">成都市云中奇境科技有限公司版权所有</div>
    </div>
  </div>
</template>

<script >
  import Vue from 'vue'
  import myHeader from '../common/header'
  import Left from '../common/left.vue'
  import {mapState,mapMutations,mapActions} from 'vuex';
  import '!style-loader!css-loader!less-loader!./style.less';

  export default {
    name: 'index',
    data() {
      return {
        footerIsShow:true, // 公共底部是否显示
        module: '' // 模块
      }
    },
    components: {
      myHeader,Left
    },
    computed: {
      /*catch store.data*/
      ...mapState({
          userInfo: state => state.login.userInfo
      })
    },
    mounted  () {

    },
      methods: {

      }
    }
</script>



