<template>
    <div class="orderDetails">
        <el-row class="row">
            <el-col class="form_item" :span="8">
                订单编号：{{detailsData.bill_info.bill_code}}
            </el-col>
            <el-col class="form_item" :span="8">
                所在区域：{{detailsData.bill_info.region_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                销售人员：{{detailsData.bill_info.sale_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                顾客姓名：{{detailsData.bill_info.guest_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                顾客手机号：{{detailsData.bill_info.guest_tel}}
            </el-col>
        </el-row>
        
        <el-row class="row" style="border:0;">
            <el-col class="form_item" :span="8">
                <h3>支付信息</h3>
            </el-col>
            <el-col class="form_item" :span="8" :offset="8">
                状态：
                <span v-if="detailsData.bill_info.finsh_tag =='0'">未结账 </span>
                <span v-if="detailsData.bill_info.finsh_tag =='1'">已结账 </span>
                <span v-if="detailsData.bill_info.finsh_tag =='2'">已撤单 </span>
            </el-col>
        </el-row> 
        <el-row class="row" style="border:0;">
            <el-col class="form_item" :span="8">
                订单金额：¥ {{detailsData.pay_info.bill_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                应收金额：¥ {{detailsData.pay_info.should_receive_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                会员优惠：¥ {{detailsData.pay_info.vip_sale}}
                <!-- <el-badge :value="2" class="item" >
                    &nbsp;
                </el-badge> -->
            </el-col>
            <el-col class="form_item" :span="8" style="color:#F56C6C">
                已收金额：¥ {{detailsData.pay_info.had_receive_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                菜品优惠：¥ {{detailsData.pay_info.dish_sale}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8">
                订单优惠：¥ {{detailsData.pay_info.order_sale}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="16" style="margin-top:15px">
                <el-table
                    :data="detailsData.payment"
                    border
                    style="width: 100%">
                    <el-table-column
                      prop="pay_name"
                      label="支付方式"
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="pay_amount"
                      label="支付金额"
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="create_date"
                      label="支付时间"
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="cashier_name"
                      label="收银员"
                      align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <h3>商品信息</h3>
        <el-col class="form_item" :span="24" style="border-bottom: 1px solid #d7dde4; padding-bottom:30px;margin-bottom:15px;">
            <el-table
            :data="detailsData.dishes"
             :span-method="objectSpanMethod"
            border
            center
            style="width: 100%;margin-top:15px;">
                <el-table-column
                    align="center"
                    label=""
                >
                </el-table-column>
                <el-table-column
                    align="center"
                    prop="dish_name"
                    label="商品名称"
                >
                </el-table-column>
                <el-table-column
                    align="center"
                    prop="dish_qty"
                    label="数量"
                >
                </el-table-column>
                <el-table-column
                  prop="specification_name"
                  label="规格"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                    prop="sale_price"
                    label="单价"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="小计"
                    align="center">
                     <template slot-scope="scope">
                       {{scope.row.pay_num * scope.row.sale_price}}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="cashier_man"
                    label="点单员"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="order_date"
                    label="下单时间"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="结账次数"
                    align="center">
                    <template slot-scope="scope">
                       第 {{scope.row.pay_num}} 次结账
                    </template>
                </el-table-column>
                <el-table-column
                    prop="order_source"
                    label="订单来源"
                    align="center">
                </el-table-column>
            </el-table>  
        </el-col>
        <div v-for="item in detailsData.reverse_reason">
        <el-row>
            <el-col class="form_item" :span="12">
                <h3>反结账信息</h3>
            </el-col>
            <el-col class="form_item" :span="4">
                反结账次数：第{{item.pay_num}}次结账
            </el-col>
            <el-col class="form_item" :span="4">
                操作人：{{item.cashier}}
            </el-col>
            <el-col class="form_item" :span="4">
                时间：{{item.date}}
            </el-col>
        </el-row>
        <el-row>
            <el-col class="form_item row2" :span="24">
                反结账原因：{{item.name}}
            </el-col>
        </el-row>
        </div>
        <div v-for="item in detailsData.revoke_reason">
        <el-col class="form_item" :span="16">
            <h3>撤单信息</h3>
        </el-col>
        <el-col class="form_item" :span="4">
            操作人：{{item.cashier}}
        </el-col>
        <el-col class="form_item" :span="4">
            时间：{{item.date}}
        </el-col>
         <el-col class="form_item row2" :span="24">
            撤单原因：{{item.name}}
        </el-col>
        </div>
        <div v-for="item in detailsData.back_reason">
        <el-col class="form_item" :span="16">
            <h3>退菜信息</h3>
        </el-col>
        <el-col class="form_item" :span="4">
            操作人：{{item.cashier}}
        </el-col>
        <el-col class="form_item" :span="4">
            时间：{{item.date}}
        </el-col>
         <el-col class="form_item row2" :span="24">
            退菜原因：{{item.name}}
        </el-col>
        </div>
                
    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 你的引入描述
    import api from '../../../../service/orderManage/index.js'
    import {mapState,mapMutations,mapActions} from 'vuex';
    
    export default {
      name: 'powerRole',
      data() {
        return {
           detailsData:null,
            spanArr:[],
            pos:0,
        }
      },
      components: {
      },
      computed: {
          /*catch store.data*/
            ...mapState({
                token: state => state.token_,
            }),
      },
    mounted () {
        this.getOrderDetail_();
    },
      methods: {

         /**
         * 订单详情接口
         * @param 参数1说明 {arr} XXX
         * @param 参数2说明 {obj} XXX
         * @returns 返回值的类型 {boolean}
         */
        async getOrderDetail_(){
            let param={
                id:this.$route.query.id,
                token:this.token
            }
            let {data,code,error} = await api.getOrderDetail(param);
            if(code == 200){
                this.detailsData = data;
                this.getSpanArr(this.detailsData.dishes);
                window.orderDetailsData = this.detailsData ;
            }else {
                this.$utils.openMessgeTip(error.message, '提示')
            }
        },

        //table合并方法
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
          if (columnIndex === 0) {
            const _row = this.spanArr[rowIndex];
            const _col = _row > 0 ? 1 : 0;
            return {
              rowspan: _row,
              colspan: _col
            };
          }
        },
    
        //table合并row位置方法
        getSpanArr(data) {
          for (var i = 0; i < data.length; i++) {
            if (i === 0) {
              this.spanArr.push(1);
              this.pos = 0;
            } else {
              // 判断当前元素与上一个元素是否相同
              if (data[i].sale_category === data[i - 1].sale_category) {
                this.spanArr[this.pos] += 1;
                this.spanArr.push(0);
              } else {
                this.spanArr.push(1);
                this.pos = i;
              }
            }
          }
        },

      }
    }
  </script>
<style lang = "less" scoped>
    @import '../../../../globalCss/var.less';
    @import "style.less";
</style>


