/*
|--------------------------------------------------------------------------
| 组件 数据分析  path：/home/dataAnalysis
|--------------------------------------------------------------------------
|
| author：你的名字
| data： vuex.userInfo
| DOTO:  备注
|
*/
<template>
    <div class="dataAnalysis" :style="{'width':currentData.width}">
        <!--板块注释-->
            <el-table
                v-loading="loading"
                :data="currentData.tableData"
                border
                style="width: 100%"
                :max-height="currentData.hight">
                    <el-table-column
                      v-for="item in currentData.tableHead"
                      :key="item.prop"
                      :prop="item.prop"
                      :label="item.name"
                      :width="item.width"
                      align="center"
                      >
                    </el-table-column>
                    
                    <el-table-column
                      label="操作"
                      width="100"
                      align="center">
                      <template slot-scope="scope">
                        <el-button 
                        @click="operateHandle(scope.row,item)"
                         v-for="item in currentData.operate"
                         :key="item.name"
                         type="text" 
                         size="small">{{item.name}}</el-button>
                      </template>
                    </el-table-column>
                </el-table>
    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 你的引入描述
    // import dappSDK from 'dappsdk'
    export default {
      name: 'webViewTable',
      data() {
        return {
             callbackData:null,
            loading:false,
            currentData:{
                hight:'500',
                tableData:[ ],//table数据
                page:'',
                tableHead:[],//table头
                operate:[],
                width:'100%'
            },
            retrun_data:{},
        }
      },
      components: {
      },
      created() {
          if(!window.Promise)
            window.Promise = new Promise();
        window.reData = this.reData;
      },
      computed: {
      },
      mounted  () {
         this.getTableInfo();
      },
      methods: {
        
        //暴露给android的方法
        reData(param){
            this.searchOperate(param);
        },

        /**
         * 与android约定好的方法
         * @param 参数1说明 命名空间
         * @param 参数2说明 函数名.
         * @param 参数3说明 调用该函数时的参数.
         * @param 参数2说明 callback
         * @returns 返回值的类型 {}
         */
        getTableInfo(){
             const that = this;
            dappSDK.exec('system', 'getTableInfo', {}, function(err,data) {
                if(err) {
                    var code = error.code;
                    if(code == '-10001'){
                         console.log('namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                     if(code == '-10002'){
                         console.log('function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                    if(code == '-10003'){
                         console.log('params错误');
                    }
                    if(code == '-10004'){
                        console.log('params错误');
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert('Error: ' + err.message)
                }
                    that.currentData = data;
                console.log(that.currentData,'that.currentData***************************');
            });
        },
        

        searchOperate(param){
            const that = this;
            dappSDK.exec('system', 'searchOperate', {page:that.page,button:param}, function(err,data) {
                if(err) {
                    var code = error.code;
                    if(code == '-10001'){
                         console.log('namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                     if(code == '-10002'){
                         console.log('function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                    if(code == '-10003'){
                         console.log('params错误');
                    }
                    if(code == '-10004'){
                        console.log('params错误');
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert('Error: ' + err.message)
                }
                that.currentData = data;
            });
        },

        /**
         * 与android约定好的方法
         * @param 参数1说明 命名空间
         * @param 参数2说明 函数名.
         * @param 参数3说明 调用该函数时的参数.
         * @param 参数2说明 callback
         * @returns 返回值的类型 {}
         */
        operateHandle(row,item){
            let that =this;
            that.retrun_data={
                  page:this.page,
                  method:item.name,
                  row_data:row
            }
            console.log(this.retrun_data)
            dappSDK.exec('system', 'operateHandle', that.retrun_data, function(err,data) {
                if(err) {
                    var code = error.code;
                    if(code == '-10001'){
                         console.log('namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                     if(code == '-10002'){
                         console.log('function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                    if(code == '-10003'){
                         console.log('params错误');
                    }
                    if(code == '-10004'){
                        console.log('params错误');
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert('Error: ' + err.message)
                }
                console.log(data,'operateHandle------------------------')
            });
        },



      }
    }
  </script>
  <style lang = "less" scoped>
    @import '../../../globalCss/var.less';
    @import "style.less";
   </style>


