<template>
    <div class="home">
       <myHeader></myHeader>
      <div class="container">
        <div class="crmContent">
            <div class="canI">
                <p class="canITip">您的账号需要激活二步验证</p>
                <p class="canITip"></p>
                <el-steps :active="1" finish-status="success" direction="vertical">
                        <el-step title="激活二步验证" description="请使用:'google身份验证器'扫描下方二维码，完成二步验证绑定">
                        </el-step>
                        <el-step title="输入二步验证" description="请输入:'google身份验证器'中的验证码，完成登录"></el-step>
                </el-steps>
                <img :src="qrcode_base64" />    
                <el-button style="margin-top: 12px;" @click="next">下一步</el-button>
            </div>            
        </div>
        <div class="common-footer">成都市云中奇境科技有限公司版权所有</div>
      </div>
    </div>
  </template>
  
  <script >
    import Vue from 'vue'
    import myHeader from '../common/header'
    import {mapState,mapMutations,mapActions} from 'vuex';
    import '!style-loader!css-loader!less-loader!./style.less';
     import {generatorTotpSecret} from '../../service/api.js'
    export default {
      name: 'index',
      data() {
        return {
            active: 1,
            qrcode_base64:'',
        }
      },
      components: {
        myHeader
      },
      computed: {
        /*catch store.data*/
        ...mapState({
            userInfo: state => state.login.userInfo
        })
      },
      mounted  () {
        this.secondaryLogin_();
      },
      methods: {
          next(){
            this.$router.push({
                path: "/yesUcan"
            });
          },

           async secondaryLogin_(){
          const {code,data,msg} =await generatorTotpSecret();
          if(code == 1000){
            this.qrcode_base64 = `data:image/png;base64,`+data.qrcodeBase64;
          }else{
            
          }
        },


      }
    }
  </script>
  


