// 添加请求拦截器 (可以在这里加token， config.headers.Authorization = `token ${store.state.token}`)
axios.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么(status == 200)
    return config;
  }, function (error) {
    // 对请求错误做些什么

    return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    if (response.data.code == 300 && response.data.message == "token失效，请重新登录") {
        confirmFun("登录超时，请重新登录","warning",()=>{location.replace("/#/")});
        return response;
    } else {
        return response;
    }   
  }, function (error) {
    // 对响应错误做点什么(status != 200)
    return Promise.reject(error);
});

export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err);
            })
            .catch((error) => {
               reject(error)
            })
    })
}

export function get(url, params) {
    let symbol = '?';
    if(url.indexOf('?') != -1){
        symbol = '&'
    }
    

    // if(store.state.login.userInfo[0].token){
    //     url = `${url}&token=${store.state.login.userInfo[0].token}`;
    // }

    return new Promise((resolve, reject) => {
        axios.get(url, params)
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err);
            })
            .catch((error) => {
               reject(error)
            })
    })
}

export default {
    get,
    post
}



