<template>
    <div class="dataAnalysis">
        <div class="head-box">
                <span class="text">web view</span>
        </div>

        <!--板块注释-->
        <div class="lineWrap">
            {{callbackData}}
        </div>
    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 你的引入描述
    import dappSDK from 'dappsdk'
    export default {
      name: 'customerInfo',
      data() {
        return {
            varA:1, // 每个data必须注释详尽
            callbackData:null,
        }
      },
      components: {
      },
      created() {
          if(!window.Promise)
            window.Promise = new Promise();
      },
      computed: {
      },
      mounted  () {
           //原生回调JS
        // var callback = {
        //     all(result){
        //         //显示回调的内容
        //         console.log(result,'result');
        //         this.callbackData=result;
        //     }
        // }
        // var getSdkInfoJson = "{\"namespace\":\"system\",\"function\":\"getSdkInfo\",\"callback\":\"callback.all\",\"params\":{}}"
        // var getLanguageSettingJson = {namespace:"system",function:"getSdkInfo",callback:callback.all,params:{age:11}};
        // this.request(getSdkInfoJson);
        // this.request(getLanguageSettingJson);
      
        // functiond();
          this.functionAfromJS();
          window.functionc =function(){
            alert('123')
         };
      },
      methods: {
    
        /**
         * 与android约定好的方法
         * @param 参数1说明 命名空间
         * @param 参数2说明 函数名.
         * @param 参数3说明 调用该函数时的参数.
         * @param 参数2说明 callback
         * @returns 返回值的类型 {}
         */
        functionAfromJS(){
            let that =this;
            dappSDK.exec('system', 'getSdkInfo', {age:11}, function(err,data) {
                if(err) {
                    var code = error.code;
                    if(code == '-10001'){
                         console.log('namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                     if(code == '-10002'){
                         console.log('function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本');
                    }
                    if(code == '-10003'){
                         console.log('params错误');
                    }
                    if(code == '-10004'){
                        console.log('params错误');
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert('Error: ' + err.message)
                }
                that.callbackData = "init";
                that.callbackData = data;
                 alert(data) //version
            });
        },


        //JS调用原生
        request(json){
                var ua = navigator.userAgent.toLowerCase();  
                if (/iphone|ipad|ipod/.test(ua)) {
                    //调用iOS
                    window.webkit.messageHandlers.dappApi.postMessage(json)
                } else if (/android/.test(ua)) {
                    //调用Android
                    window.dappApi.request(json)
                }
        },


        functionc(){
            alert('123')
         },

      }
    }
  </script>
  <style lang = "less" scoped>
    @import '../../../globalCss/var.less';
    @import "style.less";
   </style>


