let url = location.host;
let reHttp = '';
let domain = '';
if (url.indexOf('localhost:8') >= 0 || url.indexOf('192.168') >= 0) {
    reHttp = '/api';
    domain = '.kqmis.com'
} else  {
    reHttp = '192.168.188.116/api'
    domain = '.kqmis.com'
}

export default 'http://192.168.188.116:81'


