<template>
    <div class="home">
       <myHeader></myHeader>
      <div class="container">
        <div class="crmContent">
            <div class="canI">
                <p class="canITip">您的账号需要激活二步验证</p>
                <p class="canITip"></p>
                <el-steps :active="2" finish-status="success" direction="vertical">
                        <el-step title="激活二步验证" description="请使用:'google身份验证器'扫描下方二维码，完成二步验证绑定">
                        </el-step>
                        <el-step title="输入二步验证" description="请输入:'google身份验证器'中的验证码，完成登录"></el-step>
                </el-steps>
                <div class='inputY'>
                  <label>验证码</label>
                  <input type="text" v-model="yan" placeholder="请输入验证码"/>  
                </div>    
                <el-button style="margin-top: 12px;" @click="secondaryLogin_()">登录</el-button>
            </div>            
        </div>
        <div class="common-footer">成都市云中奇境科技有限公司版权所有</div>
      </div>
    </div>
  </template>
  
  <script >
    import Vue from 'vue'
    import myHeader from '../common/header'
    import {mapState,mapMutations,mapActions} from 'vuex';
    import '!style-loader!css-loader!less-loader!./style.less';
    import {secondaryLogin} from '../../service/api.js'
    export default {
      name: 'index',
      data() {
        return {
            active: 1,
            yan:null
        }
      },
      components: {
        myHeader
      },
      computed: {
        /*catch store.data*/
        ...mapState({
            userInfo: state => state.login.userInfo
        })
      },
      mounted  () {
  
      },
      methods: {
        async secondaryLogin_(){
          let param={
            code:this.yan
          };
          const {code,data,msg} =await secondaryLogin(param);
          if(code == 1000){
            this.$router.push({
                path: "/home"
            });
          }else{
            alert(msg);
          }
        },

      }
    }
  </script>
  


